










let inputrun;
export default {
	props: ['name', 'unit', 'placeholder', 'type', 'defaultData'],
	data() {
		return {
			state: ''
		}
	},
	onReady() {
		if(this.defaultData){
			this.state = this.defaultData;
		}
	},
	watch: {
		defaultData(n, o) {
			if(n){
				this.state = n;
			}
		}
	},
	methods: {
		compReset() {
			this.state = '';
		},
		inputInfo(i) {
			let vl = i.detail.value;
			
			clearTimeout(inputrun);
			inputrun = setTimeout(() => {
				this.$emit('inputChange', vl, this.type);
			}, 500)
		}
	}
}



