











export default {
	props: ['name', 'placeholder'],
	data() {
		return {
			area: ''
		}
	},
	computed: {
		fontSize() {
			let size = 0;
			switch(this.name.length) {
				case 4:
					size = 26;
				break;
				case 5:
					size = 20;
				break;
			}
			return size;
		},
		info() {
			return this.area ? this.area : `请选择${this.placeholder?this.placeholder:this.name}`
		},
		activeColor() {
			return this.area ? '#000000' : '#999999'
		}
	},
	methods: {
		areaChange(r) {
			console.log(r);
			this.area = r.detail.value.join(' ');
		}
	}
}



